body,
html {
  margin: 0;
  padding: 0;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.adminbackground {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%; /* Make sure adminbackground takes full height */
  position: relative;
}

.adminbackground::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: url("../images/constructionbackground.jpg");
  background-size: cover;
  background-position: center;
  filter: blur(5px);
  z-index: -1;
}

.container {
  margin: 20px;
  width: 100%;
  margin-top: 160px;
  max-width: 1200px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  padding: 20px;
}

.table-responsive {
  overflow-x: auto;
}

.table {
  width: 100%;
  border-collapse: collapse;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  font-size: 16px;
  color: #333;
}

.table th,
.table td {
  padding: 12px 15px;
  text-align: center;
  border: 1px solid #ddd;
}

.table th {
  background-color: #f4f4f4;
  font-weight: bold;
}

.table tbody tr:nth-child(even) {
  background-color: #f9f9f9;
}

.table tbody tr:nth-child(odd) {
  background-color: #fff;
}

.table tbody tr:hover {
  background-color: #e0f7fa;
}

.table thead th {
  position: -webkit-sticky; /* for Safari */
  position: sticky;
  top: 0;
  background-color: #f4f4f4;
  z-index: 2;
}

.table th:not(:last-child),
.table td:not(:last-child) {
  border-right: 1px solid #ddd;
}
